import React, { useEffect } from "react";

function App() {
  return ( 
    <React.Fragment >
      <h1> Demonstration </h1>
     <Simple titel={"Die Verwendung von useEffect"} />
    </React.Fragment>
  );
}

function Simple(props) {
    const [counter, setCounter] = React.useState(0);

    useEffect(() => {
        const intervall = setInterval(() => {
            setCounter(counter + 1);
        }, 1000);
        return () => clearInterval(intervall);
    }, [counter]);

    return ( 
      <React.Fragment >
        <h2 > { props.titel } </h2>
        <p> Der Seiteneffekt wurde { counter }x ausgef√ºhrt! </p>
      </React.Fragment>
    );
}


export default App;
